 * @LastEditors: YML
 * @LastEditTime: 2021-05-25 15:36:47
-->
<template>
  <div class="security-task">
    <box>
      <!-- 信息筛选 start -->
      <div class="search">
        <div class="search-body">
          <a-form-model
            layout="inline"
            class="search-main"
            ref="searchForm"
            :model="searchForm"
          >
            <a-form-model-item label="任务名称：" prop="name">
              <a-input
                class="default-input"
                v-model="searchForm.name"
                placeholder="请输入任务名称"
                allow-clear
              />
            </a-form-model-item>
            <a-form-model-item label="任务类型：" prop="type">
              <a-input
                class="default-input"
                v-model="searchForm.type"
                placeholder="请输入任务类型"
                allow-clear
              />
            </a-form-model-item>
            <a-form-model-item label="签收状态：" prop="signType">
              <a-select
                class="default-select"
                dropdown-class-name="default-select-dropdown"
                v-model="searchForm.signType"
                placeholder="请选签收状态"
                :getPopupContainer="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="all">
                  全部
                </a-select-option>
                <a-select-option value="1">
                  已签收
                </a-select-option>
                <a-select-option value="2">
                  未签收
                </a-select-option>
              </a-select>
            </a-form-model-item>
            <a-form-model-item label="开始时间：" prop="startTime">
              <a-date-picker
                class="default-date-picker"
                show-time
                v-model="searchForm.startTime"
                dropdown-class-name="default-date-picker-dropdown"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-model-item>
            <a-form-model-item label="结束时间：" prop="endTime">
              <a-date-picker
                class="default-date-picker"
                show-time
                v-model="searchForm.endTime"
                dropdown-class-name="default-date-picker-dropdown"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-model-item>
          </a-form-model>
        </div>
      </div>
      <!-- 信息筛选 end -->
      <!-- 编辑导航 start -->
      <div class="operation-nav">
        <div class="btn-box">
          <a-button class="operation-button">
            <span class="iconfont iconfont-14 icon-sousuo1"></span>
            <span class="btn-title">查询</span>
          </a-button>
          <a-button class="operation-button" @click="toReset">
            <span class="iconfont iconfont-18 icon-zhongzhi"></span>
            <span class="btn-title">重置</span>
          </a-button>
          <a-button class="operation-button">
            <span class="iconfont iconfont-16 icon-qianshou"></span>
            <span class="btn-title">批量签收</span>
          </a-button>
          <a-button class="operation-button">
            <span class="iconfont iconfont-14 icon-shanchu"></span>
            <span class="btn-title">批量删除</span>
          </a-button>
        </div>
      </div>
      <!-- 编辑导航 end -->
      <!-- 表格展示 start -->
      <div class="contents">
        <a-table
          class="default-table"
          :columns="columns"
          :data-source="data"
          :pagination="false"
          :row-selection="rowSelection"
          :scroll="{ y: 'calc(90vh - 18.5vw)' }"
        >
          <div slot="signType" slot-scope="text, record">
            <div class="sign" v-if="record.signType">
              已签收
            </div>
            <div class="no-sign" v-else>
              未签收
            </div>
          </div>
          <div slot="operation" class="operation" slot-scope="text, record">
            <div
              :class="{
                'count is-sign': record.signType,
                count: !record.signType
              }"
            >
              <span class="iconfont iconfont-16 icon-qianshou"></span>
              <span>签收</span>
            </div>
            <div class="count" @click="getMessage()">
              <span class="iconfont iconfont-18 icon-yanjing"></span>
              <span class="title">查看</span>
            </div>
            <div class="count" @click="deleteMessage()">
              <span class="iconfont iconfont-16 icon-del"></span>
              <span class="title">删除</span>
            </div>
          </div>
        </a-table>
      </div>
      <!-- 表格展示 end -->
      <!-- 分页 start -->
      <div class="content-pagination">
        <pagination
          ref="pagination"
          :total="100"
          @change="onPaginationChange"
        />
      </div>
      <!-- 分页 end -->
    </box>
    <!-- 编辑弹窗 start -->
    <editmodal
      :title="'编辑信息'"
      :visible="editModal"
      @close="closeModal"
    ></editmodal>
    <!-- 编辑弹窗 end -->
    <!-- 查看弹窗 start -->
    <editmodal
      :title="'查看详情'"
      :visible="examineModal"
      @close="closeModal"
    ></editmodal>
    <!-- 查看弹窗 end -->
    <!-- 删除弹窗 start -->
    <deletemodal :visible="deleteModal" @close="closeModal"></deletemodal>
    <!-- 删除弹窗 end -->
  </div>
</template>

<script>
import Box from "../../components/Box";
import Pagination from "../../components/Pagination";
import editmodal from "./editModal";
import deletemodal from "../../components/deleteModal";
export default {
  components: {
    Box, //边框组件
    Pagination, //分页组件
    editmodal, //新增、编辑、查看弹窗
    deletemodal //删除弹框
  },
  data() {
    return {
      pageSize: 10, // 每页条数
      page: 1, // 当前页
      searchForm: {
        name: "", //任务名称
        type: "", //任务类型
        signType: undefined, //签收状态
        startTime: null, //开始时间
        endTime: null //结束时间
      },
      columns: [
        {
          title: "任务名称",
          dataIndex: "name",
          key: "name",
          width: "8vw"
        },
        {
          title: "任务类型",
          dataIndex: "type",
          key: "type",
          width: "8vw"
        },
        {
          title: "任务等级",
          dataIndex: "grade",
          key: "grade",
          width: "8vw"
        },
        {
          title: "开始时间",
          dataIndex: "startTime",
          key: "startTime",
          width: "12vw"
        },
        {
          title: "结束时间",
          dataIndex: "endTime",
          key: "endTime",
          width: "12vw"
        },
        {
          title: "签收状态",
          dataIndex: "signType",
          key: "signType",
          width: "8vw",
          scopedSlots: { customRender: "signType" }
        },
        {
          title: "操作",
          dataIndex: "operation",
          key: "operation",
          width: "13vw",
          scopedSlots: { customRender: "operation" }
        }
      ], //表头信息
      data: [], // 表格内容
      selectedRowKeys: [], // , // 列表项是否可选择
      editModal: false, //编辑操作弹窗是否显示
      examineModal: false, //查看操作弹窗是否显示
      deleteModal: false //删除操作弹窗是否显示
    };
  },
  computed: {
    /**
     * 列表项是否可选择
     */
    rowSelection() {
      const { selectedRowKeys } = this;
      return {
        selectedRowKeys,
        onChange: this.onSelectChange,
        hideDefaultSelections: true
      };
    }
  },
  mounted() {
    //设置分页数据
    this.$refs.pagination.setData(this.page, this.pageSize);
    // 加载表格数据
    this.setList();
  },
  methods: {
    /**
     * 向列表项追加内容
     */
    setList() {
      let data = [];
      for (let i = 0; i < this.pageSize; i++) {
        let item = {
          key: i, //列表key值
          name: `安保任务${i}`, //任务名称
          type: "车辆黑名单", //任务类型
          grade: "一级", //任务等级
          signType: i % 2 == 1 ? true : false, //签收状态
          startTime: "2021-05-08 10:46:20", //开始时间
          endTime: "2021-05-12 10:46:20" //结束时间
        };
        data.push(item);
      }
      this.data = data;
    },
    /**
     * 点击重置按钮
     */
    toReset() {
      this.$refs.searchForm.resetFields();
    },
    /**
     * 列表项选中状态
     */
    onSelectChange(selectedRowKeys) {
      console.log("selectedRowKeys changed: ", selectedRowKeys);
      this.selectedRowKeys = selectedRowKeys;
    },
    /**
     * 点击×号关闭弹框
     */
    closeModal() {
      this.addModal = false;
      this.editModal = false;
      this.examineModal = false;
      this.deleteModal = false;
    },
    /**
     * 点击编辑打开弹窗
     */
    editMessage() {
      this.editModal = true;
    },
    /**
     * 点击查看打开弹窗
     */
    getMessage() {
      this.examineModal = true;
    },
    /**
     * 点击删除打开弹窗
     */
    deleteMessage() {
      this.deleteModal = true;
    },
    /**
     * 分页事件
     */
    onPaginationChange(page, pageSize) {
      console.log(page, pageSize);
      this.pageSize = pageSize;
      this.setList();
    },
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
