<!--
 * @Description: 编辑弹窗
 * @Autor: YML
 * @Date: 2021-05-14 11:53:32
 * @LastEditors: YML
 * @LastEditTime: 2021-05-20 16:06:12
-->
<template>
  <div class="edit-modal">
    <modal
      width="44.58vw"
      height="30.78vw"
      :title="title"
      @close="$emit('close')"
      :visible="visible"
    >
      <img
        slot="modalBg"
        class="img-edit"
        src="../../assets/img/components/img-edit2.png"
        alt=""
      />
      <a-form-model layout="inline" class="form-edit">
        <a-form-model-item label="任务名称：">
          <a-input
            class="default-input"
            placeholder="请输入任务名称"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="任务类型：">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择任务类型"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              任务类型1
            </a-select-option>
            <a-select-option value="2">
              任务类型2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="任务等级：">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择任务等级"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              任务等级1
            </a-select-option>
            <a-select-option value="2">
              任务等级2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="签收状态：">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择签收状态"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              已签收
            </a-select-option>
            <a-select-option value="2">
              未签收
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="开始时间：">
          <a-date-picker
            class="default-date-picker"
            show-time
            dropdown-class-name="default-date-picker-dropdown"
            :disabled="isShow"
            :get-calendar-container="triggerNode => triggerNode.parentNode"
          />
        </a-form-model-item>
        <a-form-model-item label="结束时间：">
          <a-date-picker
            class="default-date-picker"
            show-time
            dropdown-class-name="default-date-picker-dropdown"
            :disabled="isShow"
            :get-calendar-container="triggerNode => triggerNode.parentNode"
          />
        </a-form-model-item>
      </a-form-model>
      <div class="line" v-show="!isShow"></div>
      <!-- 取消和保存按钮 start -->
      <div class="operation-btn" v-show="!isShow">
        <a-button type="default" class="form-button" @click="$emit('close')">取消</a-button>
        <a-button type="primary" class="form-button save-button">保存</a-button>
      </div>
      <!-- 取消和保存按钮 end -->
    </modal>
  </div>
</template>

<script>
import Modal from "../../components/Modal";
export default {
  components: {
    Modal //弹窗组件
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      isShow: this.title == "查看详情"
    };
  },
  methods: {}
};
</script>

<style lang="less"></style>
